<template>
  <div id="QualityControl">
    <div class="controlBox">
      <div class="left">
        <div class="queryBtns">
          <Button type="primary" size="small" icon="md-cloud-download" @click="handleExport" class="exportBtn">导出</Button>
        </div>
        <Table border stripe :loading="loading" :columns="columns1"
               :data="data1" :maxHeight="tableHeight"></Table>
      </div>
      <div class="right">
        <BarChart :data="data1"></BarChart>
        <Spin size="large" fix v-if="spinShow"></Spin>
      </div>
    </div>
  </div>
</template>

<script>
import BarChart from './BarChart.vue'
import {
  // 统计数据
  getControlList,
  // 导出
  examineQualityExport
} from '@/api/quality'
export default {
  name: 'QualityControl',
  data () {
    return {
      /* 查询列表 */
      /* 表格高度 */
      tableHeight: window.innerHeight - 244,
      /* 表格数据 */
      columns1: [
        {
          title: '序号',
          key: 'index',
          width: 100,
          align: 'center',
          render: (h, params) => {
            return h('span', params.index + 1)
          }
        },
        {
          title: '核查人',
          align: 'center',
          minWidth: 200,
          key: 'name'
        },
        {
          title: '斑块总数',
          align: 'center',
          key: 'totalCheckNum',
          minWidth: 120
        },
        {
          title: '待审核斑块数量',
          align: 'center',
          minWidth: 140,
          key: 'uncheckNum'
        },
        {
          title: '已审核通过斑块数量',
          align: 'center',
          minWidth: 150,
          key: 'checkNum'
        },
        {
          title: '审核不通过斑块数量',
          align: 'center',
          minWidth: 150,
          key: 'nocheckNum'
        },
        {
          title: '审核通过率(%)',
          align: 'center',
          minWidth: 150,
          key: 'checkRate'
        }
      ],
      data1: [],
      /* 加载 */
      loading: false,
      spinShow: false
    }
  },
  components: {
    BarChart
  },
  created () {
    /* 获取列表信息 */
    this.getTableList()
  },
  methods: {
    /* axios: 查询列表 */
    getTableList () {
      this.spinShow = true
      this.loading = true
      getControlList({
        data: {}
      }).then((res) => {
        if (res.data.code === '0000') {
          this.data1 = []
          if (res.data.data.length > 0) {
            res.data.data.forEach((item, index) => {
              this.data1.push({
                name: item.uname, // 审核人
                checkNum: item.emaminedNum, // 审核
                uncheckNum: item.unEmaminedNum, // 未审核
                nocheckNum: item.backNum, // 审核不通过
                totalCheckNum: item.tzNum, // 核查斑块总数
                checkRate: (item.passRate * 100).toFixed(2) // 核查斑块总数
              })
            })
          }
          this.spinShow = false
          this.loading = false
        } else {
          this.$Notice.error({ title: res.data.msg })
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 导出文件 */
    handleExport () {
      examineQualityExport({}).then((res) => {
        if (res.data.code === '0000') {
          let data = res.data.data
          if (!data) {
            return
          }
          let link = document.createElement('a')
          link.style.display = 'none'
          let url = res.data.data.filepath
          link.href = '/epr' + url
          link.setAttribute('download', url.split('/')[url.split('/').length - 1])
          document.body.appendChild(link)
          link.click()
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    }
  }
}
</script>

<style lang="scss" rel="stylesheet/scss" type="text/scss" scoped>
  #QualityControl {
    padding: 15px;
    width: 100%;
    height: 100%;
    .controlBox {
      width: 100%;
      height: 100%;
      overflow: hidden;
      .left {
        float: left;
        width: 49%;
        height: 100%;
      }
      .right {
        position: relative;
        float: right;
        width: 49%;
        height: 100%;
      }
    }
    .exportBtn {
      background: #2d579b;
      border-color: #2d579b;
      margin-right: 10px;
      &:hover {
        background: #1a3a6f;
        border-color: #1a3a6f;
      }
    }
    .queryBtns {
      text-align: right;
      margin-bottom: 10px;
    }
  }
</style>
